import React from "react";
import ReactDOM from "react-dom";
import { nanoid } from "nanoid";

const forms = {
  display: "flex",
  justifyContent: "center",
  flexDirection: "row",
};
const form = {
  display: "flex",
  justifyContent: "center",
  flexDirection: "column",
};
const inputForm = {
  margin: 20,
  fontSize: 25,
  height: 40,
  width: 400,
  alignSelf: "center",
  paddingLeft: 10,
};
const inputBtn = {
  margin: 20,
  fontSize: 20,
  height: 30,
  width: 100,
  alignSelf: "center",
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      userReg: "",
      passReg: "",
      userLog: "",
      passLog: "",
      isRegistrated: false,
    };
    this.handleRegistration = this.handleRegistration.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
  }

  usernameRegistration = (value) => {
    const username = value;
    this.setState({
      userReg: username.replace(/\s+/g, ""),
    });
    console.log(this.state.userReg);
  };

  passwordRegistration = (event) => {
    this.setState({
      passReg: event.target.value.replace(/\s+/g, ""),
    });
  };

  handleRegistration(event) {
    if (this.state.userReg !== "" && this.state.passReg !== "") {
      console.log(this.state);
      const allUsers = [...this.state.users];
      this.setState(
        {
          users: [
            {
              userReg: this.state.userReg,
              passReg: this.state.passReg,
            },
            ...allUsers,
          ],
          userReg: "",
          passReg: "",
        },
        () => {
          localStorage.setItem("users", JSON.stringify(this.state.users));
        }
      );
    }
    event.preventDefault();
  }

  usernameLogin = (value) => {
    const username = value;
    this.setState({
      userLog: username.replace(/\s+/g, ""),
    });
    console.log(this.state.userLog);
  };

  passwordLogin = (event) => {
    this.setState({
      passLog: event.target.value.replace(/\s+/g, ""),
    });
  };

  handleLogin() {
    this.state.users.find((user, index) => {
      if (
        user.nameReg === this.state.userLog &&
        user.passReg === this.state.passLog
      ) {
        this.setState({
          userReg: this.state.userLog,
          passReg: this.state.passLog,
          isRegistrated: true,
        });
      }
      return true;
    });
  }

  render() {
    return (
      <div>
        <div style={forms}>
          <form style={form} onSubmit={this.handleRegistration}>
            <label>Registrate</label>
            <input
              style={inputForm}
              type="text"
              value={this.state.userReg}
              onChange={(event) =>
                this.usernameRegistration(event.target.value)
              }
              placeholder="Enter Username"
            ></input>
            <input
              style={inputForm}
              type="password"
              value={this.state.passReg}
              onChange={this.passwordRegistration}
              placeholder="Enter Password"
            ></input>
            <button style={inputBtn}>Submit</button>
          </form>
          <form style={form} onSubmit={this.handleLogin}>
            <label>Login</label>
            <input
              style={inputForm}
              type="text"
              value={this.state.userLog}
              onChange={(event) => this.usernameLogin(event.target.value)}
              placeholder="Enter Username"
            ></input>
            <input
              style={inputForm}
              type="password"
              value={this.state.passLog}
              onChange={this.passwordLogin}
              placeholder="Enter Password"
            ></input>
            <button style={inputBtn}>Submit</button>
          </form>
        </div>
        Show if Login Succesfull
        <div hidden={this.state.isRegistrated === false ? true : false}>
          LOGIN SUCCESFULL
        </div>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
